lor: $accordion-heading-foreground-color;\n\n            // &:hover, &:focus {\n            //     @include transition();\n            //     @include darken-on-hover-focus($accordion-heading-background-color);\n            // }\n\n            &::after {\n                content: \"\\276F\";\n                width: 1em;\n                height: 1em;\n                text-align: center;\n                // @include transition();\n            }\n        }\n\n        &-content {\n            max-height: 0;\n            padding: 0 1em;\n            background-color: $accordion-heading-foreground-color;\n            color: black;\n            // @include transition();\n            font-family: 'Body', sans-serif;\n\n            p {\n                margin: 0;\n            }\n        }\n    }\n\n    input {\n        position: absolute;\n        opacity: 0;\n        z-index: -1;\n\n        &:hover, &:focus {\n            &~label {\n                background-color: darken($accordion-heading-background-color, 10%);\n            }\n\n        }\n\n        &:checked {\n            + .tab-label {\n                background-color: darken($accordion-heading-background-color, 10%);\n                &::after {\n                    transform: rotate(90deg);\n                }\n            }\n            ~ .tab-content {\n                max-height: 100vh;\n                padding: 1em;\n            }\n        }\n    }\n}","@import '../variables/all-variables';\n\n.breadcrumbs {\n    height: 2rem;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: flex-start;\n    position: sticky;\n    top: 4rem;\n    z-index: 17;\n    background-color: $breadcumbs-background-color;\n\n    @include media-breakpoint-down(sm) {\n        display: grid;\n    }\n\n    .breadcrumb {\n        display: inline-block;\n        position: relative;\n        height: 1rem;\n        margin: 0 0.5rem 0 0.5rem;\n        color: $breadcumbs-foreground-color;\n        white-s